import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import Button from '../Button/Button';
import "./PerguntasFrequentes.css"

function PerguntasFrequentes() {
  return (
    <div className='perguntasFrequenteSec position-relative py-5'>
        <Container>
        <div className='perguntasFrequenteInner'>
          <div className='perguntasHeading ml-5 mb-4'>
             <h2 className='ml-5 pl-5'>Perguntas frequentes</h2>
          </div>
          <div className='redLineBottom position-relative'></div>
        <Row className='mt-5'>
            <Col md={4}>
              <div className='perguntasFrequentesLeft'>
                  
                  <div className='perguntasImg position-relative'>
                    <img src="Assets/perguntasFreq.jpg" alt="perguntasFreq" />
                    </div>
              </div>
           </Col>
           <Col md={8}>
              <div className='perguntasFrequentesRight pl-5'>
                  <div className='frequentesItem mb-5'>
                      <h5 className='mb-4'>O que  uma dieta cetognica?</h5>
                      <p>A dieta cetognica  um plano alimentar que de forma resumida elimina a maioria dos alimentos  ricos em carboidratos, como po e arroz, e aumenta o consumo de alimentos ricos em gorduras boas, alm de ser importante manter uma boa quantidade de protenas na alimentao. </p>
                  </div>
                  <div className='frequentesItem mb-5'>
                      <h5 className='mb-4'>O que consiste a cetose?</h5>
                      <p>A cetose  um processo natural do organismo que tem como objetivo a produo de energia a partir de gordura. Isso ocorre quando o corpo elimina quase completamente o consumo de carboidrato. </p>
                  </div>
                  <div className='frequentesItem mb-5'>
                      <h5 className='mb-4'>O mtodo  seguro?</h5>
                      <p>Na maioria dos casos sim, mas  muito importante que quem esteja passando por tratamento de diabetes, presso alta, ou esto amamentando, consulte um mdico antes de iniciar com a dieta cetognica. </p>
                  </div>
                  <div className='frequentesItem mb-5'>
                      <h5 className='mb-4'>Leva quanto tempo at o corpo parar de desejar carboidratos?</h5>
                      <p>Geralmente leva de 1 a 4 semanas antes de reprogramar o seu corpo e liberar o seu organismo do desejo de consumir carboidratos.  </p>
                  </div>
                  <div className='frequentesItem mb-5'>
                      <h5 className='mb-4'>Se eu comer muita gordura, isso no me far engordar?</h5>
                      <p>Esse mito existe h muito tempo, porque parece fazer sentido. No entanto, cientificamente, comer gordura (boa) no engorda (dentro do recomendado). O acar e os carboidratos  que so os grandes viles.</p>
                  </div>
                 
              </div>
           </Col>
        </Row>
        <div className='text-center'>
                    <Button btnText="QUERO FAZER PARTE DO MTODO" />
                    </div>
        </div>
           
       </Container>
    </div>
  )
}

export default PerguntasFrequentes
